var db = wx.cloud.database()
var users = db.collection('user')
//引进公用登录js
import {login} from '../../common/list.js'
export default{
	name:'my',
	data(){
		return {
			username:[],
			wxlofin: false
		}
	},
	
	methods:{
		getUserInfo(event){
			console.log(event)
			let user = event.detail.userInfo
			login(user)
			.then((res) => {
				console.log('登录成功')
				this.ifLogin()
			}).catch((err) => {
				console.log(err)
			})
		},
		
		ifLogin(){
			users.get()
			.then((res) => {
				console.log(res)
				if(res.data.length === 0){
					this.wxlofin = false
				}else{//已登录
					this.wxlofin = true
					this.username = res.data
				}
			}).catch((err) => {
				console.log
			})
		}
	},
	
	//每次进来判断用户是否已登录
	onShow() {
		this.ifLogin()
	}
	
}

